<template>
  <nav class="navbar navbar-expand-lg position-relative">
    <div class="container-fluid">
      <a class="navbar-brand" href="#">
        <img src="../assets/logo/Logo3.png" class="logo2 ms-3" alt=""
      /></a>
      <button
        class="navbar-toggler"
        type="button"
        data-bs-toggle="collapse"
        data-bs-target="#navbarSupportedContent"
      >
        <i class="bi bi-text-right fs-1 text-light"></i>
      </button>
      <div class="collapse navbar-collapse" id="navbarSupportedContent">
        <ul class="navbar-nav ms-auto mb-2 mb-lg-0">
          <li class="nav-item">
            <a
              class="nav-link active text-light"
              aria-current="page"
              @click="goprofile()"
              data-path=""
            >
              Home
            </a>
          </li>
          <li class="nav-item">
            <a
              class="nav-link text-light"
              data-path="services"
              @click="goprofile()"
              >Services</a
            >
          </li>
          <li class="nav-item">
            <a
              class="nav-link text-light"
              data-path="aboutus"
              @click="goprofile()"
              >About Us</a
            >
          </li>
          <li class="nav-item">
            <a
              class="nav-link text-light"
              data-path="contact"
              @click="goprofile()"
              >ContactUs</a
            >
          </li>
          <li class="nav-item">
            <a
              href=""
              class="nav-link cart main-text bg-light rounded-pill center-items p-1 inline-block mx-2"
            >
              <i class="bi bi-cart2 fs-5"></i>
              <span class="cartnum">{{ increasecar }}</span>
            </a>
          </li>
          <li class="nav-item">
            <a
              class="nav-link text-light p-1 user mx-2"
              data-path="profile"
              @click="goprofile()"
            >
              <i class="bi bi-person-circle fs-2"></i>
            </a>
          </li>
        </ul>
      </div>
    </div>
  </nav>
</template>

<script>
export default {
  name: "navigation",
  data() {
    return {
      // gostatic: false,
    };
  },
  created() {
    window.addEventListener("scroll", () => {
      if (window.pageYOffset > 90) {
        document.querySelector("nav").style.position = "fixed; !important";
      } else {
        document.querySelector("nav").style.position = "";
      }
    });
  },
  methods: {
    goprofile() {
      let navItemList = document.querySelectorAll(".nav-link");
      navItemList.forEach((e) => {
        e.addEventListener("click", () => {
          navItemList.forEach((el) => {
            if (el.classList.contains("active")) {
              el.classList.remove("active");
            }
          });
          e.classList.add("active");
          if (this.$route.path !== "/" + e.dataset.path)
            this.$router.push("/" + e.dataset.path);
        });
      });
    },
  },
  computed: {
    increasecar() {
      return this.$store.state.counter;
    },
  },
};
</script>

<style>
.navbar {
  z-index: 999;
  transition: 0.3s;
  background-color: var(--secondcolor);
  position: fixed !important;
  top: 0px;
  left: 0px;
  width: 100%;
  padding: 0px !important;
}
.navbar .navbar-nav {
  align-items: center;
}
/* .navbar.gostatic {
  
  background-color: var(--secondcolor);
  padding-top: 0px !important;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5) !important;
} */
.navbar img.logo2 {
  height: 90px;
  width: 130px;
}
/* .navbar.gostatic .nav-link {
  margin: 0px 10px !important;

} */
.nav-link {
  font-size: 18px;
  letter-spacing: 1.3px;
  font-weight: normal !important;
  padding: 5px 10px !important;
  position: relative;
  cursor: pointer;
}
.nav-link::after {
  position: absolute;
  content: "";
  width: 0%;
  height: 2px;
  background-color: var(--maincolor);
  left: 50%;
  bottom: -2px;
  transform: translateX(-50%);
  transition: 0.3s;
}
.nav-link:hover::after {
  width: 50%;
}
.nav-link.active {
 color: var(--maincolor) !important;
}
.navbar-toggler:focus {
  box-shadow: none !important;
}
@media (max-width: 991px) {
  .navbar-nav {
    position: fixed;
    top: 80px;
    left: 0px;
    height: 100vh;
    width: 350px;
    background-color: var(--secondcolor);
    align-items: center;
    justify-content: center;
    gap: 20px;
    transform: translateX(-100%);
    animation: navanim 1 0.4s ease-in-out both;
    z-index: 600;
  }

  .navbar-nav .nav-link {
    width: fit-content;
    font-size: 24px;
  }
  .nav-link:hover::after {
    width: 100%;
  }
}
@keyframes navanim {
  to {
    transform: translateX(0px);
  }
}
/* .logo1{
  width: 150px;
} */
.nav-item .nav-link i {
  cursor: pointer;
}
</style>
